.four--lines{
    text-align: center;
}
.four--lines h2{
    font-size: 34px;
    color: #609;
    font-family: "Roboto", sans-serif;
    font-weight: bold;
}
.four--lines__box{
	text-align: center;
	display: block;
	margin: 5px auto;
}
.four--lines__box img{
	display: block;
	margin: 5px auto;
	-webkit-transform: scale(1);
	    -ms-transform: scale(1);
	        transform: scale(1);
    -webkit-transition: -webkit-transform .5s cubic-bezier(0.22, 0.61, 0.36, 1);
    transition: -webkit-transform .5s cubic-bezier(0.22, 0.61, 0.36, 1);
    -o-transition: transform .5s cubic-bezier(0.22, 0.61, 0.36, 1);
    transition: transform .5s cubic-bezier(0.22, 0.61, 0.36, 1);
    transition: transform .5s cubic-bezier(0.22, 0.61, 0.36, 1), -webkit-transform .5s cubic-bezier(0.22, 0.61, 0.36, 1);
}
.four--lines__box img:hover{
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}
.four--lines__box p{
	text-align: center;
    font-family: "Roboto", sans-serif;
    font-weight: 300;
    font-size: 24px;
    color: #666;
}
.four--lines__box p strong{
    font-family: "Roboto", sans-serif;
    font-weight: bold;
    color: #404041;
}
@media(max-width:480px){
    .four--lines h2{
        font-size: 30px;
    }
}


